import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../../Utils/Functions/nextPage";
// import { customDebounce } from "../../../../Utils/Functions/debounce";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import Loading from "../../../../Loader/Loading";
import { useAdmissionMergedApplicationFeesList } from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import CombineFeesCollectionCard from "./CombineFeesCollectionCard";
import CombineSearchWrapper from "../CombineSearchWrapper";

const CombineFeesCollection = ({ instituteId, aid, financeId }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const {
    admissionMergedApplicationFeesList,
    admissionMergedApplicationFeesListLoading,
    admissionMergedApplicationFeesListRefetch,
  } = useAdmissionMergedApplicationFeesList({
    data: {
      aid: aid,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !aid,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (admissionMergedApplicationFeesList?.list?.length) {
      let obj = nextPage(admissionMergedApplicationFeesList?.list?.length);
      setState(() => obj);
    }
  }, [admissionMergedApplicationFeesList?.list?.length]);

  // const onDebounce = useCallback(
  //   customDebounce((val) => {
  //     setPage(() => 1);
  //     setSearch(val);
  //   }, 1500),
  //   []
  // );

  const onSearchEvent = (e) => {
    if (
      !admissionMergedApplicationFeesListLoading &&
      admissionMergedApplicationFeesList?.list?.length >= 0
    ) {
      // if (e.target.value) {
      setSearchCopy(e.target.value);
      // } else {
      //   setSearchCopy("");
      //   setSearch("");
      // }
    }
    // if (e.target.value) onDebounce(e.target.value);
    // else {
    //   setPage(() => 1);
    //   setSearch("");
    // }
  };

  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };
  const onRefetch = useCallback(() => {
    setPage(() => 1);
    admissionMergedApplicationFeesListRefetch();
  }, []);
  return (
    <>
      <CombineSearchWrapper>
        <input
          type="text"
          placeholder={t("search_applicants")}
          onChange={onSearchEvent}
          onKeyUp={onCallBindSearch}
          value={searchCopy}
        />
      </CombineSearchWrapper>

      {admissionMergedApplicationFeesList?.list?.map((applicant, index) =>
        admissionMergedApplicationFeesList?.list?.length === index + 1 ? (
          <div key={applicant?._id} ref={ref}>
            <CombineFeesCollectionCard
              applicant={applicant}
              instituteId={instituteId}
              onRefetch={onRefetch}
              appId={applicant?.student?.new_app?.appId}
              financeId={financeId}
            />
          </div>
        ) : (
          <CombineFeesCollectionCard
            applicant={applicant}
            key={applicant?._id}
            instituteId={instituteId}
            onRefetch={onRefetch}
            appId={applicant?.student?.new_app?.appId}
            financeId={financeId}
          />
        )
      )}
      {admissionMergedApplicationFeesListLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <>{page === 1 ? <QvipleLoader /> : <Loading />}</>
        )
      ) : search ? (
        admissionMergedApplicationFeesList?.list?.length === 0 && (
          <EmptyMessage
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
            title="No admission applicants found related this search."
          />
        )
      ) : (
        admissionMergedApplicationFeesList?.list?.length === 0 && (
          <EmptyMessage
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
            title="No admission applicants list."
          />
        )
      )}
    </>
  );
};

export default CombineFeesCollection;
