import { useLocation, useNavigate, useParams } from "react-router-dom";
import style from "../../OneOngoingAdmission/OneOngoingAdmission.module.css";
import { useCallback, useState } from "react";
import {
  imageShowUrl,
  pdfCdnUrl,
} from "../../../../Services/UrlConfig/BaseUrl";
import {
  assestsFeedUrl,
  assestsUserAdmissionUrl,
  assestsUserFinanceUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import dayjs from "dayjs";
import { useTranslation } from "react-i18next";
import SelectFeeStructure from "../../../FinanceManager/FeesStructure/SelectFeeStructure/SelectFeeStructure";
import {
  useAdmissionOngoingApplicationDocumentEditStructure,
  useAdmissionOngoingApplicationDocumentRevert,
} from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import AdmissionButton from "../../../../Ui/Button/AdmissionButton";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import DocumentCollectDocs from "../../OneOngoingAdmission/DocumentCollection/DocumentCollectDocs";
const CombineDocumentCollectionCard = ({
  instituteId,
  applicant,
  did,
  bid,
  cmid,
  appId,
  onRefetch,
}) => {
  const { t } = useTranslation();
  const params = useParams();
  const navigate = useNavigate();
  const getQuery = useLocation();

  const [step, setStep] = useState(0);
  const [disabled, setDisabled] = useState(false);
  const [admissionOngoingApplicationDocumentEditStructure] =
    useAdmissionOngoingApplicationDocumentEditStructure();
  const [admissionOngoingApplicationDocumentRevert] =
    useAdmissionOngoingApplicationDocumentRevert();
  const onViewApplicantProfile = useCallback(() => {
    navigate(`/q/${params.username}/student/profile`, {
      state: {
        sid: applicant?.student?._id,
        viewAs: "ADMISSION_ADMIN",
        instituteId: instituteId,
        whichUrl:
          getQuery.state?.urlOffSet === "Institute" ? "INSTITUTE" : null,
        onlyReadView: getQuery.state?.role,
        subjectData: {
          subjectChange: "authorized",
          appId: appId,
        },
      },
    });
  }, [instituteId, applicant?.student?._id]);

  const onViewApplication = (e) => {
    e.stopPropagation();
  };
  const onFeeStructure = (e) => {
    e.stopPropagation();
    setStep(1);
  };

  const onDocument = (e) => {
    e.stopPropagation();
    setStep(2);
  };
  const onClose = () => {
    setStep("");
  };

  const onEditStrucutre = (feeStructure) => {
    if (applicant?.student?._id && appId && feeStructure?._id) {
      setDisabled((pre) => !pre);
      admissionOngoingApplicationDocumentEditStructure({
        sid: applicant?.student?._id,
        appId: appId,
        applicant: {
          fee_struct: feeStructure?._id,
          staffId: getQuery.state?.sid,
        },
      })
        .then(() => {
          onRefetch();
          onClose();
          setDisabled((pre) => !pre);
        })
        .catch(() => {
          setDisabled((pre) => !pre);
        });
    }
  };

  const onUndoEventFire = (e) => {
    e.stopPropagation();
    if (applicant?.student?._id && appId && applicant?.revert_request_status) {
      setDisabled((pre) => !pre);
      admissionOngoingApplicationDocumentRevert({
        sid: applicant?.student?._id,
        appId: appId,
        revert: {
          statusId: applicant?.revert_request_status,
          staffId: getQuery.state?.sid,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
        })
        .catch(() => {
          setDisabled((pre) => !pre);
        });
    }
  };

  // console.info("applicant?.revert_request_status", applicant);
  return (
    <>
      <div
        className={style.oac_container}
        onClick={onViewApplicantProfile}
        title="View Profile"
      >
        <div className={style.oac_container_inner}>
          <img
            alt="Student Request Avatar"
            src={
              applicant?.student?.studentProfilePhoto
                ? `${imageShowUrl}/${applicant?.student?.studentProfilePhoto}`
                : `${assestsFeedUrl}/feed-user-avatar.svg`
            }
            loading="lazy"
          />
          <div className={style.oac_container_inner_info}>
            <h6>{`${applicant?.student?.studentFirstName ?? ""} ${
              applicant?.student?.studentMiddleName
                ? applicant?.student?.studentMiddleName
                : ""
            } ${applicant?.student?.studentLastName ?? ""}`}</h6>
            <h6>
              {t("fee_structure")}
              {" : "}
              {applicant?.student?.fee_structure?.category_master
                ?.category_name ?? ""}
              {", "}
              {t("rs_only")}{" "}
              {applicant?.student?.fee_structure?.applicable_fees ?? ""}
            </h6>
            <p>
              {t("form_no_only")}: {applicant?.student?.form_no ?? ""}
            </p>
            <p>
              {applicant?.install_type
                ? applicant?.install_type
                : `
          ${t("select_on")}:
          ${dayjs(applicant?.select_on).format("DD MMM YYYY")}`}
            </p>
          </div>
        </div>

        <div className={style.oac_container_contact}>
          <div className={style.oac_container_contact_inner}>
            <img
              src={`${assestsUserAdmissionUrl}/gender.svg`}
              alt="email icon"
            />
            <p>{applicant?.student?.studentGender}</p>
          </div>

          <div className={style.oac_container_contact_inner}>
            <img
              src={`${assestsUserAdmissionUrl}/phone.svg`}
              alt="phone icon"
            />
            <p>
              {applicant?.student?.user?.userPhoneNumber
                ? applicant?.student?.user?.userPhoneNumber
                : applicant?.student?.user?.userEmail
                ? applicant?.student?.user?.userEmail
                : applicant?.student?.studentPhoneNumber
                ? applicant?.student?.studentPhoneNumber
                : applicant?.student?.studentParentsPhoneNumber
                ? applicant?.student?.studentParentsPhoneNumber
                : applicant?.student?.studentEmail ?? ""}
            </p>
          </div>
        </div>

        <div
          className={style.oac_container_stuff}
          style={{
            width: "24%",
          }}
        >
          <a
            href={`${pdfCdnUrl}/${applicant?.student?.application_print?.[0]?.value}`}
            target="_blank"
            rel="noopener noreferrer"
            style={{
              textDecoration: "none",
              marginTop: "-3px",
            }}
            onClick={onViewApplication}
          >
            <img
              src={`${assestsUserAdmissionUrl}/download.svg`}
              alt="receipt icon"
              title="View Application Form"
            />
          </a>

          <img
            src={`${assestsUserFinanceUrl}/edit.svg`}
            alt="edit icon"
            onClick={onFeeStructure}
          />
          <img
            src={`${assestsUserAdmissionUrl}/undo.svg`}
            alt="undo icon"
            style={{
              width: "30px",
              height: "30px",
            }}
            onClick={onUndoEventFire}
            title="Undo Application"
          />
          <AdmissionButton
            label="collect_docs"
            onAction={onDocument}
            customStyle={{
              padding: "0.4rem 0.7rem",
              fontSize: "13px",
            }}
          />
        </div>
      </div>
      {step === 1 ? (
        <SelectFeeStructure
          onClose={onClose}
          onSelectedStrucutre={onEditStrucutre}
          did={did}
          bid={bid}
          cmid={cmid}
          selectedStructure={applicant?.student?.fee_structure?._id}
        />
      ) : step === 2 ? (
        <DocumentCollectDocs
          onClose={onClose}
          onRefetch={onRefetch}
          applicant={applicant}
          appId={appId}
        />
      ) : null}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default CombineDocumentCollectionCard;
