import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { useAdmissionMergedApplicationDocumentList } from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import CombineDocumentCollectionCard from "./CombineDocumentCollectionCard";
import CombineSearchWrapper from "../CombineSearchWrapper";
import { nextPage } from "../../../../Utils/Functions/nextPage";
// import { customDebounce } from "../../../../Utils/Functions/debounce";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";

const CombineDocumentCollection = ({ instituteId, aid }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const {
    admissionMergedApplicationDocumentList,
    admissionMergedApplicationDocumentListLoading,
    admissionMergedApplicationDocumentListRefetch,
  } = useAdmissionMergedApplicationDocumentList({
    data: {
      aid: aid,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !aid,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (admissionMergedApplicationDocumentList?.list?.length) {
      let obj = nextPage(admissionMergedApplicationDocumentList?.list?.length);
      setState(() => obj);
    }
  }, [admissionMergedApplicationDocumentList?.list?.length]);

  // const onDebounce = useCallback(
  //   customDebounce((val) => {
  //     setPage(() => 1);
  //     setSearch(val);
  //   }, 1500),
  //   []
  // );

  const onSearchEvent = (e) => {
    if (
      !admissionMergedApplicationDocumentListLoading &&
      admissionMergedApplicationDocumentList?.list?.length >= 0
    ) {
      // if (e.target.value) {
      setSearchCopy(e.target.value);
      // } else {
      //   setSearchCopy("");
      //   setSearch("");
      // }
    }
    // if (e.target.value) onDebounce(e.target.value);
    // else {
    //   setPage(() => 1);
    //   setSearch("");
    // }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };
  const onRefetch = useCallback(() => {
    setPage(() => 1);
    admissionMergedApplicationDocumentListRefetch();
  }, []);
  return (
    <>
      <CombineSearchWrapper>
        <input
          type="text"
          placeholder={t("search_applicants")}
          onChange={onSearchEvent}
          onKeyUp={onCallBindSearch}
          value={searchCopy}
        />
      </CombineSearchWrapper>

      {admissionMergedApplicationDocumentList?.list?.map((applicant, index) =>
        admissionMergedApplicationDocumentList?.list?.length === index + 1 ? (
          <div key={applicant?._id} ref={ref}>
            <CombineDocumentCollectionCard
              applicant={applicant}
              instituteId={instituteId}
              did={applicant?.student?.new_app?.applicationDepartment}
              bid={applicant?.student?.new_app?.applicationBatch}
              cmid={applicant?.student?.new_app?.applicationMaster}
              onRefetch={onRefetch}
              appId={applicant?.student?.new_app?.appId}
            />
          </div>
        ) : (
          <CombineDocumentCollectionCard
            applicant={applicant}
            key={applicant?._id}
            instituteId={instituteId}
            did={applicant?.student?.new_app?.applicationDepartment}
            bid={applicant?.student?.new_app?.applicationBatch}
            cmid={applicant?.student?.new_app?.applicationMaster}
            onRefetch={onRefetch}
            appId={applicant?.student?.new_app?.appId}
          />
        )
      )}
      {admissionMergedApplicationDocumentListLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <>{page === 1 ? <QvipleLoader /> : <Loading />}</>
        )
      ) : search ? (
        admissionMergedApplicationDocumentList?.list?.length === 0 && (
          <EmptyMessage
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
            title="No admission applicants found related this search."
          />
        )
      ) : (
        admissionMergedApplicationDocumentList?.list?.length === 0 && (
          <EmptyMessage
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
            title="No admission applicants list."
          />
        )
      )}
    </>
  );
};

export default CombineDocumentCollection;
