import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import { assestsUtilityUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import HorizontalTabCard from "../../../Ui/Tab/Horizontal/HorizontalTabCard";
import HorizontalTabWrapper from "../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import { useCallback } from "react";
import style from "../../FinanceManager/FinanceManager.module.css";
import { useTranslation } from "react-i18next";
import CombineDocumentCollection from "./CombineDocumentCollection/CombineDocumentCollection";
import CombineFeesCollection from "./CombineFeesCollection/CombineFeesCollection";
import CombineAdmissionConfirm from "./CombineAdmissionConfirm/CombineAdmissionConfirm";
import FinanceReceipt from "../../FinanceManager/TransactionHistory/FinanceReceipt";
const AdmissionCombinedApplicationConfig = ({
  aid,
  instituteId,
  financeId,
}) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();

  const onNavigate = useCallback(() => {
    let url = "";

    url =
      getQuery.state?.urlOffSet === "Institute"
        ? `/q/${params.username}/institute/member/admission`
        : `/q/${params.username}/member/staff/admission`;
    navigate(url, {
      state: getQuery.state,
    });
  }, [navigate, getQuery.state, params.username]);

  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <div
              className={style.fm_header_start_container}
              style={{
                gap: "0.2rem",
              }}
            >
              <img
                alt="search icon"
                src={`${assestsUtilityUrl}/back.svg`}
                style={{
                  height: "2rem",
                  width: "2rem",
                }}
                onClick={onNavigate}
              />
              <h6>{t("combined_application")}</h6>
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <HorizontalTabWrapper>
          {tabList.map((tab) => (
            <HorizontalTabCard
              key={tab?.id}
              tab={tab}
              isLink
              isActive={tab.groupIs?.includes(searchParams.get("n"))}
              parentState={getQuery.state}
              url={
                getQuery.state?.urlOffSet === "Institute"
                  ? `/q/${params.username}/institute/member/admission?type=combine-application${tab.urlKey}`
                  : `/q/${params.username}/member/staff/admission?type=combine-application${tab.urlKey}`
              }
            />
          ))}
        </HorizontalTabWrapper>
        <BorderBottom
          customStyleBorder={{
            marginTop: "0",
          }}
        />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0.2rem",
          }}
        >
          {searchParams.get("n") === "fees" ? (
            <CombineFeesCollection
              aid={aid}
              instituteId={instituteId}
              financeId={financeId}
            />
          ) : searchParams.get("n") === "confirmed" ? (
            <CombineAdmissionConfirm aid={aid} instituteId={instituteId} />
          ) : searchParams.get("n") === "receipt" ? (
            <FinanceReceipt instituteId={instituteId} />
          ) : (
            <CombineDocumentCollection aid={aid} instituteId={instituteId} />
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
    </>
  );
};

export default AdmissionCombinedApplicationConfig;
const tabList = [
  {
    label: "docs_collection",
    id: "document",
    urlKey: "",
    groupIs: ["", null],
  },
  {
    label: "fees_collection",
    id: "fees",
    urlKey: "&n=fees",
    groupIs: ["fees"],
  },
  {
    label: "receipts",
    id: "receipt",
    urlKey: "&n=receipt",
    groupIs: ["receipt"],
  },
  {
    label: "confirmed",
    id: "confirmed",
    urlKey: "&n=confirmed",
    groupIs: ["confirmed"],
  },
];
